.school-general-content {
  background: #FAFAFA;
  @include size(240px, 289px);
  display: inline-block;
  position: relative;

  .event-content {
    font-size: 14px;
    margin-top: 12px;
    width: 204px;
    height: 210px;
    text-align: center;
    background-image: url("/static/img/school-events/school/icon.png");
    position: absolute;
    margin: auto;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;


    .content-top {
      color: #FFF;
      display: table-cell;
      font-size: 18px;
      font-weight: bold;
      height: 84px;
      width: 200px;
      vertical-align: middle;

      .event-content-title.big {
        font-size: 24px;
      }
    }



    .event-statistic {
      display: table-cell;
      font-size: 14px;
      font-weight: bold;
      height: 120px;
      width: 200px;
      vertical-align: middle;
      color: #834E00;
      font-weight: bold;

      .number {
        font-size: 60px;
      }

      .wait-time {
        font-size: 36px;
        color: rgba(0,0,0,0.54);
      }
    }
  }
}
