$events-color: #FF800E;
$company-nav-bg-color: #FFF;
$company-nav-height: 70px;
$company-nav-item-container-size: 32px;
$company-nav-item-size: 36px;

.center {
    position: absolute;
    margin: auto;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}
.company-nav-container {
    display: inline-block;
    font-size: 0;
    height: 68px;
    margin: 84px auto 0 auto;
    /* transform: skew(-20deg); */
  /* width: 96%; */
    text-align: center;

    div {
        display: inline-block;
        vertical-align: top;
    }
    .company-nav-left {
        background: url("/static/img/school-events/guide/guide-left.png");
        display: inline-block;
        height: $company-nav-height;
        width: 154px;
    }
    .nav-content {
        background: url("/static/img/school-events/guide/guide-middle.png");
        background-repeat: repeat-x;
    }

    .company-trigger-list {
        height: $company-nav-height;
    }

    .company-item-container {
        box-sizing: border-box;
        height: $company-nav-height;
        width: $company-nav-item-container-size;
        padding: 11px 0;
        position: relative;
    }

    .company-logo {
        cursor: pointer;
    }

    .company-item {
        @extend .center;
        background: $events-color;
        background: url("/static/img/school-events/guide/c-sina2.png");
        background-repeat: no-repeat;
        height: $company-nav-item-size;
        width: $company-nav-item-size;
    }

    .company-item-cutting {
        background: url("/static/img/school-events/guide/guide-line.png");
        height: $company-nav-height - 10 ;
        width: 20px;
    }

    .company-right-action {
        height: 70px;
        width: 154px;
        position: relative;

        .button-container {
            /* display: table-cell; */
            /* vertical-align: middle; */
        }
        .action-button {
            @extend .center;
            background: url("/static/img/school-events/btn/joinschool-normal.png");
            display: inline-block;
            height: 38px;
            width: 105px;
            /* vertical-align: middle; */
            &:hover {
                background: url("/static/img/school-events/btn/joinschool-hover.png");
            }
        }
    }

    .company-nav-right {
        background: url("/static/img/school-events/guide/guide-right.png");
        display: inline-block;
        height: 70px;
        width: 54px;
    }
}
/* // 适应性 */
.company-nav-container {
  @include max-screen($phone-screen) {
    display: none;
  }
}
