.header {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  background: url("/static/img/school-events/page-header-bg.jpg");
  background-repeat: no-repeat;
  text-align: center;

  img {
    width: 100%;
    height: auto;
  }

  .header-title {
    margin: 0 auto;
    padding-top: 132px;
    width: 100%;
    height: 100%;
    max-width: 503px;
    max-height: 420px;


    .header-title-content {
      width: 100%;
      height: 100%;
      /* background-image: url("/static/img/school-events/header-title.png");
         background-size: 100% 100%; */
      display: inline-block;
    }
  }

  .header-info {
    position: relative;
    width: 100%;
    height: 100%;
    max-width: 701px;
    max-height: 104px;
    margin: 0 auto;
    margin-top: 24px;

    .info-content {
      position: absolute;
      left: 285px;
      top: 5px;
    }
  }

  .header-action {
    background-image: url("/static/img/school-events/btn/activityjoin-normal.png");
    cursor: pointer;
    display: inline-block;
    height: 43px;
    margin-top: 40px;
    width: 136px;

    &:hover {
      background: url("/static/img/school-events/btn/activityjoin-hover.png");
    }
  }
}

.header-event-progress {
  margin-top: 36px;
  padding: 0 160px;

  &.mobile {
    display: none;
  }

  .progress-title {
    color: $event-font-color1;
    display: inline-block;
    font-size: 30px;
    text-align: center;
    width: 80%;
    max-width: 282px;
  }

  .progress-container {
    position: relative;
  }

  .progress-line-caption {
    padding: 0 130px;
    font-size: 0;
    position: absolute;
    top: 52px;
    left: 0;
    width: 100%;
    z-index: -1;

    .line-item {
      border-bottom: 1px solid $event-font-color2;
      color: $event-font-color2;
      display: inline-block;
      font-size: 12px;
      padding-bottom: 12px;
      text-align: center;
      width: 33.33%;
    }
  }

  .progress-content {
    padding-top: 32px;
    font-size: 0;
    text-align: center;
  }
}

$progress-icon-size: 22px;

.progress-item {
  display: inline-block;
  font-size: 0;
  letter-spacing: 3px;
  padding: 0px 2%;
  line-height: 20px;
  width: 25%;
  vertical-align: top;

  .item-title {
    color: $event-font-color2;
    font-size: 14px;
    font-weight: bold;
    text-align: center;
  }

  .item-icon-container {
    width: 100%;
    padding-top: 20px;
    text-align: center;

    .item-icon {
      @include size($progress-icon-size);
      @include border-radius($progress-icon-size/2);
      border: 1px solid $event-font-color2;
      display: inline-block;
      line-height: $progress-icon-size;
      text-align: center;
      background: #F4A801;

      .item-dot {
        @include size(4px);
        @include border-radius(2px);
        background: $event-font-color2;
        display: inline-block;

      }
    }
  }


  .item-content {
    color: $event-font-color2;
    font-size: 12px;
    padding-top: 26px;
  }
}

@import './media/header';
