$events-color: #FF800E;
$company-card-content-width: 368px;
/* 多出字符按三个圆点显式 */
.text-ellipisis{
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.company-list {
  text-align: center;

  .company-item {
    display: inline-block;
    margin: 0 auto;
    font-size: 0;
    @include border-radius(2px);
    @extend .z-depth-1;
  }

}

.company-card {
  background: #FFF;
  display: inline-block;
  font-size: 0;
  height: 256px;
  text-align: left;
  vertical-align: top;
  width: 480px;
}

.company-info-top {
  font-size:  14px;
  height: 64px;
  color: rgba(0,0,0,0.87);
  .logo-container {
    display: inline-block;
    vertical-align: top;
    @include size(64px);
    background: #FAFAFA;
  }

  .info-item {
    line-height: 32px;
    max-width: 160px;
    @extend .ellipsis;
  }

  .company-info {
    display: inline-block;
    vertical-align: top;
    padding-left: 12px;
  }
  .company-name {
    font-size: 20px;
    font-weight: bold;
  }
}

.company-basic {
  border: 1px solid #E6E6E6;
  box-sizing: border-box;
  padding: 12px;
}

.company-info-bottom {
  font-size: 14px;
  color: rgba(0,0,0,0.54);
  padding-top: 12px;
  width: $company-card-content-width;

  p {
    display: block;
    display: -webkit-box;
    height: 54px;
    line-height: 18px;
    max-width: 400px;
    margin: 0 auto;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
  }

}
