$bottom-height: 152px;
.event-bottom {
  /* background-image: url("/static/img/school-events/bottom/bottom-bg.jpg"); */
  margin-top: 128px;
  height: 329px;
  max-width: 1366px;
  text-align: left;
  &.mobile {
    display: none;
  }
}

.bottom-content {
  background-color: rgba(255,255,255,0.1);
  width: 960px;
  margin: 0 auto;
  padding: 20px;

  .bottom-main {
    /* height: $bottom-height; */

    .bottom-left,.bottom-right {
      display: inline-block;
      position: relative;
      vertical-align: top;
    }

    .support-coms {
      width: 570px;
      height: 100%;
      position: relative;


      .bottom-block {
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
      }
    }

    .event-share {
      position: absolute;
      right: 0;
      top: 0;
      width: 156px;


      .bottom-block {
        padding-left: 12px;
        .share-item {
          cursor: pointer;
          display: inline-block;
          margin-right: 12px;
          vertical-align: top;

          &.weixin {
            position: relative;

            .mobile-hide {
              position: absolute;
              left: -45px;
              top: 45px;
              display: none;
            }

            &:hover {
              .mobile-hide {
                display: block;
              }
            }
          }
        }
      }
    }
  }
  .bottom-right {
    width: 232px;
    height: $bottom-height;
    margin-left: 110px;
    background: transparent;

    .info-item {
      display: inline-block;
      vertical-align: top;
      width: 110px;
      height: $bottom-height;
      position: relative;

      span {
        background: #F8C777;
        bottom: 0;
        color: $event-font-color1;
        font-weight: bold;
        display: inline-block;
        height: 24px;
        left: 0;
        line-height: 24px;
        position: absolute;
        text-align: center;
        width: 100px;
      }
    }
  }
  .bottom-title {
    font-weight: bold;
    display: inline-block;
    color: $event-font-color1;

  }
}
.bottom-title {
  padding-top: 12px;
  padding-left: 5px;
}
.bottom-faq {
  background: rgba(255,255,255,0.3);
  height: 100px;
  margin-top: 20px;

  .bottom-block {
    line-height: 24px;
    color: $event-font-color1;
    padding: 12px;
  }
}

@import 'media/bottom';
