$events-color: #FF800E;
$company-card-content-width: 368px;
/* 多出字符按三个圆点显式 */

.company-position {
  font-size: 0;

  .position-item {
    width: 33.33%;

    &.top {
      width: 50%;
    }
  }
}

.position-item {
  /* background: #FAFAFA; */
  border: 1px solid #E6E6E6;
  box-sizing: border-box;
  display: inline-block;
  font-size: 14px;
  height: 128px;
  line-height: 24px;
  padding: 20px 0 0 24px;
  vertical-align: top;

  .position-title {
    color: rgba(0,0,0,0.54);
  }

  .position-status {

    &.hot {
      color: red;
      font-weight: bold;
    }

    &.urgent {
      border: 1px solid red;
      @include size(15px);
      color: #FFF;
      background-color: red;
      display: inline-block;
      line-height: 15px;
      text-align: center;
    }
  }

  .position-salary {
    color: rgba(0,0,0,0.87);
    font-weight: bold;
  }
}

.position-total-container {

  background: $event-main-color;
  color: #FFF;
  font-weight: bold;
  height: 32px;
  line-height: 32px;
  width: 120px;
  text-align: center;
}
