.school-general-info {
  height: 100%;

  .school-icon {
    @include size(240px, 254px);
    border-bottom: 1px solid #EEE;
    display: inline-block;
    position: relative;

    .school-name {
      font-size: 24px;
      position: absolute;
      bottom: 12px;
      width: 100%;
    }

    img {
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      margin: auto;
      max-width: 100%;
      max-height: 100%;
    }
  }
}
