@import "school/schoolItem";
@import "school/schoolDetailsTop";
@import "school/schoolDetailsBottom";

.school-container {
  font-size: 0;
  width: 100%;
  max-width: 1366px;
  margin-top: 48px;
  text-align: center;

  &.mobile {
    display: none;
  }
}
.school-detail {
  display: inline-block;
  padding: 0;
  position: relative;
  vertical-align: top;
  width: 240px;

  .school-detail-item {
    /* display: none; */
    background: #FFF;
    z-index: 1;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;

    &.active {
      display: block;
      z-index: 10;
    }
  }
}

.school-action-container {
  width: 100%;
  text-align: center;

  &.mobile {
    display: none;
  }

  .school-action {
    background-image: url("/static/img/school-events/btn/bigbtn-normal.png");
    background-size: 100% 100%;
    cursor: pointer;
    display: inline-block;
    height: 43px;
    margin-top: 24px;
    width: 330px;

    &:hover {
      background-image: url("/static/img/school-events/btn/bigbtn-hover.png");
    }
  }
}
.major-site-jumper {
  width: 50%;
  padding-top: 36px;
  text-align: center;
  text-decoration: underline;
  margin: 0 auto;

  a {
    color: $event-font-color2;
    font-size: 20px;
  }
}
@import "media/school";
