.company-container {
  @include max-screen($phone-screen) {

    .company-position {
      display: none;
    }

    .company-item {
      width: 90%;
    }
    .company-card {
      min-width: auto;
      height: auto;
      padding: 0;
      width: 100%;
      border: 0;
    }

    .company-info-top {
      height: auto;
      padding: 24px;
    }

    .company-info-bottom {
      display: none;
    }

    .company-hr {
      background: #FAFAFA;
      border-top: 1px solid #EEEEEE;
      min-width: auto;
      margin: 0;
      padding-left: 24px;

      width: 100%;
      .hr-title {
        position: absolute;
        right: 0;
        left: auto;
        width: 55px;

        span {
          padding: 0 8px;
          position: relative;
          &:before {
            content: '';
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 0 10px 20px 0;
            border-color: transparent $event-main-color transparent transparent;
            display: block;
            position: absolute;
            top: -2px;
            left: -14px;
          }
        }
      }
    }
  }
}
