@import "../../helpers/prefixer";
@import "../../helpers/mixins";
@import "../../helpers/media-queries";

@import "../../base/variables";
@import "../../base/normalize";
@import "../../base/global";

@import "../../components/notify";
@import "../../components/modal";

$notify-height: 56px;
// Mixins
$main-color: #F4A801;

$max-width: 1366px;
$event-font-color1: #834E00;
$event-font-color2: #6A3906;
$event-main-color: #F5AA01;

@mixin max-width($width) {
  width: 100%;
  max-width: $width;
}

.font-family{
  font-family: $font-family;
  *zoom:1;
  // 文字透明度设置为87%
  color:rgba(0,0,0,0.87);
}

body {
  @extend .font-family;
  background: $event-main-color;
  margin-bottom: 56px;
  overflow-x: hidden;

}

.main-container, .event-bottom, .company-container {
  padding: 0;
  @include max-width($max-width);
  text-align: center;
}

.event-success-modal {
  @include size(500px, 240px);
  background: #FFF;
  padding: 24px;

  .event-cancel {
    @include size(36px);
    @include border-radius(18px);
    background: #FFF;
    line-height: 36px;
    position: absolute;
    text-align: center;
    top: 4px;
    right: 4px;
    cursor: pointer;
    span {
      font-size: 24px;
      color: rgba(0,0,0,0.54);
    }
  }

  .event-success-modal-title {
    font-size: 24px;
    color: rgba(0,0,0,0.87);
    line-height: 36px;
  }

  .event-success-modal-sub-title {
    font-size: 16px;
    color: rgba(0,0,0,0.87);
    line-height: 36px;
  }

  .event-success-modal-caption {
    font-size: 14px;
    color: rgba(0,0,0,0.54);
    line-height: 20px;
    padding-top: 24px;

  }
  .btn {
    float: right;
    display: inline-block;
    border: none;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    -ms-border-radius: 2px;
    border-radius: 2px;
    height: 36px;
    min-width: 88px;
    line-height: 36px;
    padding: 0 12px;
    margin-right: 16px;
    outline: 0;
    font-size: 14px;
    text-transform: uppercase;
    text-decoration: none;
    text-align: center;
    letter-spacing: .5px;
    vertical-align: middle;
    -webkit-tap-highlight-color: transparent;
    cursor: pointer;
    color: white;
    background-color: #20C05C;
  }
}
