.school-badge {

  border: none;
  display: inline-block;
  height: auto;
  padding: 0;
  height: 541px;
  width: 720px;
  .badge-list {
    font-size: 0;
    padding: 0;
    text-align: left;
    background: #FFF;

    .badge-item {
      box-sizing: border-box;
      border: 1px solid #EEEEEE;
      cursor: pointer;
      display: inline-block;
      @extend .item-border;
      @include size(144px);
    }
  }
}
