@include max-screen($phone-screen) {
  img {
    max-width: 100%;
    max-height: 100%;
  }
}

.header {
  @include max-screen($phone-screen) {
    background-position: 15% 50%;
    padding: 0 12px;

    .header-title {
      padding-top: 48px;
    }

    .header-info {
      padding-top: 28px;

    }

    .header-action {
      margin-top: 52px;
    }
  }
}

.header-event-progress {
  @include max-screen($phone-screen) {
    padding: 0;

    &.pc {
      display: none;
    }

    &.mobile {
      display: block;
    }
  }
}

.event-intro {
  margin: 0 60px;
  margin-top: 48px;

  @include max-screen($phone-screen) {
    margin: 48px 0;
  }
  .intro-title {
    border-bottom: 2px solid #834E00;

    p {
      font-size: 30px;
      color: #834E00;
    }

  }
  .intro-content {
    padding: 24px 0;
  }
}

.intro-list {
  padding: 0;

  .intro-item {
    list-style-type: none;
    text-align: left;
    font-size: 0;
    padding: 12px 24px;
    position: relative;
    vertical-align: top;

    .empty-clear {
      clear: both;
    }

    .item-index {
      @include size(20px);
      @include border-radius(10px);
      background-color: #FEC03A;
      display: inline-block;
      float: left;
      text-align: center;
      left: 0;
      line-height: 20px;
      /* position: absolute; */

      span {
        color: #FFF;
        font-size: 14px;
      }
    }

    .item-content {
      width: 90%;
      font-size: 14px;
      float: right;
      /* position: absolute; */
      box-sizing: border-box;

      .content-header {
        color: #6A3906;
        display: inline-block;
        height: 24px;
        line-height: 24px;
        width: 100%;

        * {
          display: inline-block;
        }

        .header-title {
          font-size: 14px;
          font-weight: bold;
          width: 90px;
        }
        .header-extra {
          font-size: 12px;
          padding-left: 36px;
        }

      }
      .content-main {
        color: #6A3906;
        line-height: 20px;

        p {
          margin-top: 4px;
        }
      }

    }
  }
}
