/* 简寻新版本参数   */
/* 基本色值 */
/* 绿色 */
$green-1: #20C05C !default;
$green-2: #13A277 !default;
$green-3: #5BA58A !default;

/* 灰色 */
$gray-1: #FAFAFA;
$gray-2: #EEE;
$gray-2: #E6E6E6;

/* 红色 */
$red-1: #F67893;


/* 用户拒绝模块 */

$list-height: 32px !default;
$icon-height: 16px !default;

/* section C端和B端主要内容的变量 */
$side-card-min-height: 500px !default;
$section-header-height: 48px;
$section-content-header-height: 48px;

/* List */
$list-active-color: #FAFAFA !default;
$list-hover-color: #FAFAFA !default;
$list-action-hover-color: #FFF !default;
$list-action-bg-color: #E6E6E6 !default;
$jx-content-side-padding: 24px;

/* side-tab */
$side-tab-bg-color: #FAFAFA !default;
$side-tab-hover-color: #E6E6E6 !default;
$side-tab-active-color: #E6E6E6 !default;
$side-tab-border-color: #324B5E !default;

/* sidebar */
$sidebar-content-bg-color: #FAFAFA !default;
$sidebar-default-width: 230px !default;
$bottom-border-color: #E6E6E6 !default;

/* bg */
$bg-dark-light: $side-tab-hover-color;

$primary-color-green-dark: #5BA58A !default;

/* font-color */
$font-green-color: #13A277;
$font-red-color: #F67893;



/* footer */
$footer-height: 54px;

/* form  */
$checkbox-size: 12px !default;
$checkbox-border-color: rgba(0,0,0,0.54) !default;
$checkbox-active-color: #13A277 !default;
$checkbox-border-radius: 1px !default;

/* Media Query Ranges  */
$phone-screen: 480px !default;
$tablet-screen: 768px !default;
$min-screen: 320px !default;


/* notify */
$notify-height: 56px;
