#notify{
  display: inline-block;
  position: fixed;
  top: -$notify-height;
  z-index: 99999;
  width: 100%;
  border-radius: 2px;
  height: $notify-height;
  left: 0;
  opacity: 1;


  .notify-content {
    display: inline-block;
    font-size: 16px;
    position: absolute;
    margin: auto;
    height: $notify-height;
    line-height: $notify-height;
    width: 100%;
    text-align: center;
    padding: 8px;
    left: 0;
    right: 0;
  }

  &.success {
    color: #3c763d;
    background-color: #dff0d8;
    border-color: #d6e9c6;
  }

  &.errors {
    color: #a94442;
    background-color: #f2dede;
    border-color: #ebccd1;
  }
  .cross {
    cursor: pointer;
    position: absolute;
    right: 20px;
    top: 20px;
    font-size: 16px;
    height: 20px;
    width: 20px;
  }
