@include max-screen($phone-screen) {

  .school-card {
    width: 94%;
    margin: 0 auto;
  }
  /* 紧急处理 */
  /* .major-site-jumper {
     @include size(160px, 130px);
     }
   */
  .school-container.mobile {
    display: block;

  }

  .school-action-container.mobile {
    display: block;

    .school-action {
      height: 43px;
      width: 136px;

      @include mobile-hdpi("/static/img/school-events/mobile/button-normal@1x.png", "/static/img/school-events/mobile/button-normal@2x.png");
      &:hover {
        @include mobile-hdpi("/static/img/school-events/mobile/button-hover@1x.png", "/static/img/school-events/mobile/button-hover@2x.png");

      }
      &:active {
        @include mobile-hdpi("/static/img/school-events/mobile/button-pressed@1x.png", "/static/img/school-events/mobile/button-pressed@2x.png");

      }
    }
  }
}
