.event-bottom {
  @include max-screen($phone-screen) {
    background: rgba(255, 255, 255, 0.3);
    width: 90%;
    margin: 90px auto 24px auto;
    height: auto;

    &.pc {
      display: none;
    }

    &.mobile {
      display: block;
    }

    img {
      width: 100%;
      height: auto;
    }

    .bottom-faq {
      border-bottom: 3px solid rgba(247, 194, 76, 0.99);
      height: auto;
    }

    .bottom-title {
      font-weight: bold;
      display: inline-block;
      color: #9A6115;
    }

    .bottom-support {
      position: relative;

      .bottom-title {
        position: absolute;
      }
    }
  }
}
