@import 'jx-variables';
/*** Colors ***/
$primary-color: #20C05C !default;
$primary-bg-color: #FAFAFA !default;
$primary-content-color: #FFF !default;
$primary-color-light: #1FBF5B !default;
$primary-color-dark: darken($primary-color, 15%) !default;

$secondary_color: #41C76E !default;
$success-color: #41C76E !default;
$error-color: #F0B6C4 !default;
/* $link-color: color("light-blue", "darken-1") !default; */
$link-color: $green-2 !default;

/*** Badges ***/
$badge-bg-color: $secondary-color !default;

/*** Buttons ***/
$button-font-color: lighten(#FFF, 0.13) !default;
$button-color: #20C05C !default;
$button-hover-color:#20C05C !default;
$button-active-color:#13B84E !default;
$button-bg-color-disabled: #F5F6F7 !default;
$button-color-disabled: lighten(#000, 0.78) !default;

/* mini-buttons */
$mini-btn-active-color: #FFF;
$reject-color: #EC4569;
$success-color: #13A277;
$process-color: #1481B9;
$failed-color: #E5AC2F;
$notype-color: #999;


/* 线框按钮字体颜色 */
$button-font-color-flat: #13A277 !default;
$button-bg-color-flat: #FFF !default;
/* 深色线框按钮 */
$button-dark-color: rgba(56, 60, 62, 1);
/* 幽灵按钮字体颜色 */
$button-font-color-ghost: #13A277 !default;
$button-bg-color-ghost: #FFF !default;
$button-hover-color-ghost: #F5F6F7 !default;
$button-active-color-ghost: #DBE3E9 !default;
/* 不重要按钮－简单按钮 */
$button-font-color-simple: lighten(#000, 0.46) !default;
$button-bg-color-simple: #F5F6F7 !default;
$button-hover-color-simple: #DBE3E9 !default;
$button-active-color-simple: #13B84E !default;
$button-active-font-color-simple: #FFF !default;

$button-color-raised: #FFF !default;
$button-floating-size: 37px !default;
$button-height: 36px !default;
$button-width: 88px !default;
$button-font-size-shared: 1.3rem !default;
$button-line-height: 36px !default;


/*** Cards ***/
$card-padding: 0 24px 16px 24px !default;
$card-bg-color: #1FBF5B !default;
$card-list-width:360px !default;
$card-list-height:150px !default;
$card-content-logo-width:72px !default;
$normal-size: 64px !default;
$big-size: 108px !default;

$card-content-bg-color: #FFF !default;
$gray-header-color: #FAFAFA !default;
$content-list-height: 48px !default;
$content-list-title-width: 72px !default;
$content-list-content-width: 300px;
$header-right-padding: 24px !default;

$card-pic-container-size: 48px !default;
$side-menu-width: 180px !default;
/*** Collapsible ***/
$collapsible-height: 3rem !default;
$collapsible-header-color: #fff !default;
$collapsible-border-color: #ddd !default;


/*** Dropdown ***/
$dropdown-color: $secondary-color !default;

/*** Fonts ***/
$font-family: "Helveticia neue" , "Arial" , "Tahoma", "Microsoft YaHei", "微软雅黑", "Hiragino Sans GB", "冬青黑体", "SimSun","宋体", "STHeiti","华文黑体",sans-serif;
$roboto-font-path: "../font/roboto/" !default;
$icons-font-path: "../font/material-design-icons/" !default;

/*** Forms ***/
// Text Inputs + Textarea
$input-border-color: #D9D9D9 !default;
$input-error-color: $error-color !default;
$input-success-color: $success-color !default;
$input-focus-color: $secondary-color !default;
$input-bg-color: #fff !default;
$label-font-size: 12px !default;
$input-font-size: 14px !default;
$input-disabled-color: rgba(0,0,0, 0.26) !default;
$input-disabled-solid-color: #BDBDBD !default;
$input-placeholder-color: rgba(0,0,0,0.28) !default;


// Radio Buttons
$radio-fill-color: $secondary-color !default;
$radio-empty-color: #5a5a5a !default;

// Switches
$switch-bg-color: $secondary-color !default;
$switch-checked-lever-bg: desaturate(lighten($secondary-color, 25%), 25%) !default;
$switch-unchecked-bg: #F1F1F1 !default;
$switch-unchecked-lever-bg: #818181 !default;

// Date Picker
$datepicker-weekday-bg: darken($secondary_color, 7%) !default;
$datepicker-date-bg: $secondary_color !default;
$datepicker-year: rgba(255, 255, 255, .4) !default;
$datepicker-focus: rgba(0,0,0, .05) !default;
$datepicker-selected: $secondary-color !default;
$datepicker-selected-outfocus: desaturate(lighten($secondary-color, 35%), 15%) !default;


/*** Global ***/
// Media Query Ranges
$small-screen-up: 601px !default;
$medium-screen-up: 993px !default;
$large-screen-up: 1201px !default;
$small-screen: 600px !default;
$medium-screen: 992px !default;
$large-screen: 1200px !default;

$tablet: "(min-width: 768px) and (max-width: 1023px)";
$desktop: "(min-width: 1024px)";
@mixin tablet {
  @media (min-width: #{$tablet-width}) and (max-width: #{$desktop-width - 1px}) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: #{$desktop-width}) {
    @content;
  }
}

$medium-and-up: "only screen and (min-width : #{$small-screen-up})" !default;
$large-and-up: "only screen and (min-width : #{$medium-screen-up})" !default;
$small-and-down: "only screen and (max-width : #{$small-screen})" !default;
$medium-and-down: "only screen and (max-width : #{$medium-screen})" !default;
$medium-only: "only screen and (min-width : #{$small-screen-up}) and (max-width : #{$medium-screen})" !default;

// Grid Variables
$num-cols: 12 !default;
$gutter-width: 1.5rem !default;
$element-top-margin: $gutter-width/3 !default;
$element-bottom-margin: ($gutter-width*2)/3 !default;
$main-container-top: 72px;
/*** Navbar ***/
$header-height: 56px !default;
$navbar-height:48px !default;
$navbar-margin-top: $header-height - $navbar-height !default;
$navbar-font-color: rgba(0,0,0,0.54) !default;
$navbar-font-hover-color: $green-2 !default;
$navbar-bg-color: #FFF !default;
$navbar-dividor-color: #EBEBEB !default;
$navbar-dropdown-hover-color: #EBEBEB !default;
$navbar-icon-color: $primary-color !default;
/* notify */
$notify-height: $header-height;


/* Uploader */
$uploader-height: 64px !default;
$uploader-border: #9D9696 !default;
$uploader-background-color: #EBEBEB !default;
$uploader-label-color: $primary-color !default;



/*** Tabs ***/
$tabs-underline-color: $primary-color-light !default;
$tabs-text-color: $primary-color !default;
$tabs-bg-color: #fff !default;

/*** Tables ***/
$table-border-color: #d0d0d0 !default;
$table-striped-color: #f2f2f2 !default;

/*** Toasts ***/
$toast-height: 48px !default;
$toast-color: #323232 !default;
$toast-text-color: #fff !default;

/*** Typography ***/
$off-black: rgba(0, 0, 0, 0.87) !default;
// Header Styles
$h1-fontsize: 4.2rem !default;
$h2-fontsize: 3.56rem !default;
$h3-fontsize: 2.92rem !default;
$h4-fontsize: 2.28rem !default;
$h5-fontsize: 1.64rem !default;
$h6-fontsize: 1rem !default;

// Footer
$footer-bg-color: $primary-color !default;

// Flowtext
$range : $large-screen - $small-screen !default;
$intervals: 20 !default;
$interval-size: $range / $intervals !default;

/*** Collections ***/
$collection-border-color: #e0e0e0 !default;
$collection-bg-color: #fff !default;
$collection-active-bg-color: $secondary-color !default;
$collection-active-color: lighten($secondary-color, 55%) !default;
$collection-hover-bg-color: #ddd !default;

/* Progress Bar */
$progress-bar-color: $secondary-color !default;

/* text */
$collection-text-color: #1FBF5B !default;
