@charset "UTF-8";
//Default styles

*,
*:before,
*:after {
  box-sizing: inherit;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  color: $link-color;
  text-decoration: none;
  // Gets rid of tap active state
  -webkit-tap-highlight-color: transparent;
}

ul {
  padding: 0;
  li {
    list-style-type: none;
  }
}

html{
  height:100%;
  min-width:320px;
  width: 100%;
}

body {
  height: 100%;
  position: relative;
  background: $primary-bg-color;
  width: 100%;
  min-width: 320px;
}

header {
  width: 100%;
  min-width: 320px;
}

.website-container {
  position: relative;
  min-height: 100%;
  padding-bottom: $footer-height + 12;
  height: auto;

  @include max-screen($phone-screen) {
    padding-bottom: $footer-height * 2 + 12;
  }
}
.main-container{
  width: 960px;
  position: relative;
  padding: 64px 0 0 0;
  margin: 0 auto;
}
ul {
  list-style-type: none;
}

