// Positioning
.valign-wrapper {
  @include flexbox();
  @include align(center);

  .valign {
    display: block;
  }
}

// classic clearfix
.clearfix {
  clear: both;
}


// Z-levels
/* 投影1 */
/* 距离: 1; */
/* 大小: 5; */
/* 不透明度: 10; */
/* 光照角度: 30°; */
/* ---------------- */
/* 外发光 */
/* 颜色: #000000; */
/* 不透明度: 2; */
/* 大小: 4 */
.z-depth-1{
  @include box-shadow-2(-1.7px 1px 5px 0 rgba(0, 0, 0, 0.1), 0 0 4px 0 rgba(0, 0, 0, 0.02));
}
/* .z-depth-1-half{ */
/*     @include box-shadow-2(0 5px 11px 0 rgba(0, 0, 0, 0.18), 0 4px 15px 0 rgba(0, 0, 0, 0.15)); */
/* } */
/* 投影2 */
/* 距离: 4; */
/* 大小: 10; */
/* 不透明度: 12; */
/* 光照角度: 30°; */
/* ---------------- */
/* 外发光 */
/* 颜色: #000000; */
/* 不透明度: 4; */
/* 大小: 6 */
.z-depth-2{
  @include box-shadow-2(-6.8px 4px 10px 0 rgba(0, 0, 0, 0.12), 0 0 6px 0 rgba(0, 0, 0, 0.06));
}
/* 投影导航栏 */
/* 距离: 2; */
/* 大小: 4; */
/* 不透明度: 10; */
/* 光照角度: 90°; */
.z-depth-nav{
  @include prefix(box-shadow,0 2px 4px 0 rgba(0,0,0,0.1));
}
