$events-color: #FF800E;
$company-card-content-width: 368px;
/* 多出字符按三个圆点显式 */
.text-ellipisis{
    display: inline-block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.company-hr {
    border-top: 1px solid $event-main-color;
    font-size: 0;
    margin-top: 18px;
    position: relative;
    width: $company-card-content-width;

    .hr-title {
      font-size: 12px;
      height: 20px;
      left: 0;
      background:  $event-main-color;
      display: inline-block;
      padding: 2px 4px;

      span {
        color: #FFF;
      }
    }

    .hr-info {
        padding: 12px 0;

        * {
            display: inline-block;
            vertical-align: top;
        }
    }

    .hr-avatar-container {
        @include size(50px);
        background: transparent;
        margin-right: 28px;
        img {
            max-width: 100%;
            max-height: 100%;
        }
    }

    .hr-name, .hr-position {
        font-size: 14px;
        line-height: 50px;
        color: rgba(0,0,0,0.87);
    }
}
